<template lang="pug">
  el-row
    el-col(:span="16")
      h2 {{isEdit ? '' : ''}}

      el-form(ref="form", :model="form", label-width="80px")
        el-form-item(label="")
          el-input(v-model="form.name")
        el-form-item(label="")
          el-input(v-model="form.desc")
        el-form-item(label="")
          el-upload(
            action="index.php/index/admin/product/saveThumb",
            type="drag",
            :thumbnail-mode="true",
            :on-success="onSuccess"
          )
        el-form-item
          el-button(type="primary", @click="onSubmit") {{isEdit ? '' : ''}}
          el-button(@click="back") 

</template>

<style>
</style>

<script>
  import mixins from '../mixins.js'
  const {add} = mixins
  console.log(add)

  export default {
    mixins: [add],
    data () {
      return {
        form: {
          name: '',
          desc: '',
          thumb: ''
        },
        getApi: '/admin/product/read'
      }
    },

    computed: {
      api () {
        return this.isEdit ? '/admin/product/update' : '/admin/product/add'
      }
    },
    methods: {
      onSuccess (response, file, fileList) {

      }
    },
    mounted () {
      this.getInfo();
    }
  }
</script>


