<template lang="pug">
  div
    h2 
    el-table(:data="tableData")
      el-table-column(
        label="",
      )
        template(scope="scope")
          span {{scope.row.name}}
      el-table-column(
        label="",
      )
        template(scope="scope")
          span {{scope.row.nickname}}
      el-table-column(
        label="",
      )
        template(scope="scope")
          span {{scope.row.email}}
      el-table-column(
        label="",
      )
        template(scope="scope")
          span {{scope.row.create_time}}
      el-table-column(
        label="",
      )
        template(scope="scope")
          span {{scope.row.is_disable === 0 ? '' : ''}}
      el-table-column(
        label="",
      )
        template(scope="scope")
          el-button(type="text", size="small", @click="edit(scope.row.id)") 
          el-button(type="text", size="small", @click="disable(scope.row.id)") 
    el-button-group(style="margin-top: 24px;")
      el-button(@click="add") 
</template>

<style>
</style>

<script>
  export default {
    data () {
      return {
        tableData: [],
        page: 1
      }
    },
    methods: {
      async getData () {
        let {code, msg} = await this.$ajax({
          api: '/admin/user/list',
          data: {
            page: 1
          }
        })

        this.tableData = msg.data
      },

      add () {
        this.$router.push({name: 'user_add'})
      },
      edit (id) {
        this.$router.push({name: 'user_edit', params: {id}})
      },

      async disable (id) {
        let {code, msg} = await this.$ajax({
          api: '/admin/user/disable',
          data: {
            id
          }
        })
        if (code === 0) {

          this.$message({
            message: `${msg.name}${msg.result === 0 ? '' : ''}`,
            type: 'success'
          })
          this.getData()
        } else {
          this.$message({
            message: '',
            type: 'error'
          })
        }
      }
    },
    mounted () {
      this.getData()
    }
  }
</script>


